/* General Section Styling */
section {
  position: relative;
  width: 100vw;
  box-sizing: border-box;
  padding: 60px 40px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #e0e5ec;
  color: #000;
  margin-bottom: 80px; /* spacing between sections */
}

/* Certification Section */
#certification-section {
  margin-bottom: 100px; /* specifically increase gap before contact section */
  z-index: 0;
}

/* Contact Info Section */
.contact-info-section {
  position: relative;
  width: 100vw;
  min-height: 100vh;
  margin-left: 220px;
  padding: 40px 0;
  text-align: center;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  overflow-x: hidden;
  z-index: 0;
  border-radius: 20px;
  box-shadow: 0 4px 24px #e0e5ec, 0 1.5px 6px #bec4d0;
  margin-bottom: 2.5rem;
}

/* Remove background overlay */
.contact-info-section::before {
  content: none;
}

/* Title styling */
.section-title {
  font-size: 2.5rem;
  margin-bottom: 30px;
  font-weight: 700;
  color: #1e90ff;
  border-bottom: 4px solid #1e90ff;
  padding-bottom: 8px;
  text-shadow: 1px 1px 0 #ffffff;
}

.section-title .highlight {
  color: #f94144;
}

/* Cards container */
.info-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
  width: 100%;
  max-width: 1200px;
  margin-top: 20px;
}

/* Individual card with neumorphism */
.info-card {
  background: #f5f7fa;
  padding: 28px 22px;
  width: 260px;
  border-radius: 16px;
  box-shadow: 0 2px 8px #e0e5ec;
  transition: box-shadow 0.2s, transform 0.2s;
  color: #222;
  text-align: center;
  cursor: pointer;
  margin-bottom: 20px;
  min-height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.info-card:hover {
  transform: translateY(-6px) scale(1.03);
  box-shadow: 0 8px 32px #c1c6d4, 0 2px 8px #ffffff;
}

/* Icon inside card */
.info-icon {
  width: 48px;
  margin-bottom: 15px;
  filter: drop-shadow(2px 2px 2px rgba(30, 144, 255, 0.10));
}

/* Link styling inside cards */
.info-link {
  color: #1e90ff;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s;
}

.info-link:hover {
  color: #f94144;
  text-decoration: underline;
}

/* Responsive for mobile */
@media (max-width: 768px) {
  .contact-info-section {
    margin-left: 0;
    max-width: 100vw;
    padding: 1.2rem 0.5rem;
    border-radius: 12px;
  }

  .info-cards {
    flex-direction: column;
    gap: 18px;
  }

  .info-card {
    width: 98vw;
    padding: 1rem 0.5rem;
    border-radius: 12px;
  }

  .info-icon {
    width: 36px;
  }

  .section-title {
    font-size: 1.5rem;
    padding-bottom: 4px;
    margin-bottom: 16px;
  }
}

@media (max-width: 900px) {
  .info-cards {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .info-card {
    width: 90%;
    margin-bottom: 20px;
    min-height: 160px;
  }
}

@media (min-width: 769px) {
  .contact-info-section {
    margin-left: 220px;
    max-width: calc(100vw - 220px);
  }

  #certification-section {
    margin-left: 220px;
    max-width: calc(100vw - 220px);
  }
}
