.projects-section {
  position: relative;
  width: 100vw;
  margin-left: 0;
  padding: 60px 0 30px 0;
  box-sizing: border-box;
  font-family: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #222;
  background: none;
  min-height: 100vh;
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.section-title {
  font-size: 2.2rem;
  margin-bottom: 25px;
  font-weight: 700;
  color: #1e90ff;
  border-bottom: 4px solid #1e90ff;
  padding-bottom: 10px;
}

.section-subtitle {
  font-size: 1.1rem;
  color: #555;
  margin-bottom: 32px;
  font-style: italic;
}

/* Cards container */
.project-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 32px;
  width: 100%;
  max-width: 900px;
}

/* Individual project card with neumorphism */
.project-card {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px #e0e5ec, 0 1.5px 6px #bec4d0;
  padding: 2rem 1.2rem 1.5rem 1.2rem;
  width: 320px;
  transition: box-shadow 0.2s, transform 0.2s;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #222;
  border: none;
  cursor: pointer;
}

.project-card:hover {
  transform: translateY(-8px) scale(1.03);
  box-shadow: 0 8px 32px #c1c6d4, 0 2px 8px #ffffff;
}

/* Icon image */
.project-icon {
  width: 180px;
  height: 180px;
  object-fit: contain;
  margin-bottom: 18px;
  border-radius: 12px;
  box-shadow: 0 2px 8px #1e90ff22;
}

/* Project title */
.project-card h3 {
  font-size: 1.25rem;
  margin-bottom: 12px;
  color: #1e90ff;
}

/* Project description */
.project-card p {
  font-size: 1rem;
  color: #555;
  line-height: 1.5;
}

/* Responsive */
@media (max-width: 768px) {
  .projects-section {
    padding: 30px 0 10px 0;
  }

  .section-title {
    font-size: 1.5rem;
  }

  .section-subtitle {
    font-size: 0.98rem;
    margin-bottom: 18px;
  }

  .project-cards {
    flex-direction: column;
    gap: 18px;
  }

  .project-card {
    width: 98vw;
    padding: 1rem 0.5rem 1rem 0.5rem;
    border-radius: 12px;
  }

  .project-icon {
    width: 90vw;
    height: auto;
    max-width: 220px;
  }
}
